<template lang="pug">
  div(class="beginner-guide-root")
    transition(name="fade" mode="out-in" v-on:after-leave="afterLeave")
      div(v-if="currentScenes === 24" :key="'scene40'" @click="nextScenes" :style="{'background-image':`url(${require('@/assets/image/beginnerGuide/story-10.png')})`}")
        p 人類開始定居於各星球中，<br>然而過了數年之後，<br>發現外太空資源匱乏
      div(v-if="currentScenes === 25" :key="'scene41'" @click="nextScenes" :style="{'background-image':`url(${require('@/assets/image/beginnerGuide/story-11.png')})`}")
        p(:style="{'color': '#EEEEEE'}") 此時科學家透過望遠鏡觀測到<br>地球上的波波拉漸漸消退...
      div(v-if="currentScenes === 26" :key="'scene42'" @click="nextScenes" :style="{'background-image':`url(${require('@/assets/image/beginnerGuide/story-12.png')})`}")
        p(:style="{'color': '#EEEEEE'}") 走投無路的人類，<br>決定鋌而走險，回到地球採集資源
      div(v-if="currentScenes === 27" :key="'scene43'" @click="nextScenes" :style="{'background-image':`url(${require('@/assets/image/beginnerGuide/story-13.png')})`}")
        p 慶幸的是，波波拉狀態趨於穩定，<br>雖然有些地區的波波拉仍然失控，<br>但只要是波波拉穩定的區域，<br>人類皆可採集資源
</template>

<script>

export default {
  props: {
    currentScenes: Number
  },
  data () {
    return {
      canClick: true,
      module: null
    }
  },
  created: function () {
    import('@/game/scenes/PlayScene').then(async (module) => {
      this.module = module
      this.module.gameFunction.scene.input.manager.enabled = false
    })
  },
  beforeDestroy: function () {
    navigator.geolocation.clearWatch(this.watcher)
    this.module.gameFunction.scene.input.manager.enabled = true
  },
  methods: {
    nextScenes () {
      if (this.canClick) {
        this.canClick = false
        this.$emit('nextScenes', '故事')
      }
    },
    afterLeave (el) {
      this.canClick = true
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.beginner-guide-root {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: #000000;
  > div {
    width: 100%;
    height: 100%;
    background-position: center;
    > p {
      font-size: 14px;
      line-height: 24px;
      color: #000000;
      padding-top: 40px;
    }
  }
}

.fade-enter-active {
  transition: all 0.8s;
}
.fade-leave-active {
  transition: all 1s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
</style>
